// 底部导航栏组件
import tabBar from "./components/tabBar.vue"
Vue.component("tabBar", tabBar)


// 导航守卫
router.beforeEach((to, from, next) => {
    if (
        to.path == "/login" ||
        to.path == "/backups" ||
        to.path == "/backmnemonics" ||
        to.path == "/createAddress" ||
        to.path == "/backTerms" ||
        to.path == "/importAddress"
    ) {
        next();
    } else if (!localStorage.getItem("token")) {
        next({
            path: "/login"
        });
    } else {
        next();
    }
});



