<template>
  <div class="tabbar">
    <div @click="goRouter(1)">
      <img v-if="status=='1'" src="../../static/img/tabbar/home.png" alt />
      <img v-else src="../../static/img/tabbar/homeCopy.png" alt />
      <span :class="{'choose':status=='1'}">首页</span>
    </div>
    <div @click="goRouter(2)">
      <img v-if="status=='2'" src="../../static/img/tabbar/quotation.png" alt />
      <img v-else src="../../static/img/tabbar/quotationCopy.png" alt />
      <span :class="{'choose':status=='2'}">行情</span>
    </div>
    <div @click="goRouter(3)">
      <img v-if="status=='3'" src="../../static/img/tabbar/transaction.png" alt />
      <img v-else src="../../static/img/tabbar/transactionCopy.png" alt />
      <span :class="{'choose':status=='3'}">交易</span>
    </div>
    <div @click="goRouter(4)">
      <img v-if="status=='4'" src="../../static/img/tabbar/assets.png" alt />
      <img v-else src="../../static/img/tabbar/assetsCopy.png" alt />
      <span :class="{'choose':status=='4'}">资产</span>
    </div>
    <div @click="goRouter(5)">
      <img v-if="status=='5'" src="../../static/img/tabbar/user.png" alt />
      <img v-else src="../../static/img/tabbar/userCopy.png" alt />
      <span :class="{'choose':status=='5'}">我的</span>
    </div>
  </div>
</template>

<script>
export default {
  name: 'tab-bar',
  props: ['status'],
  data() {
    return {}
  },
  methods: {
    goRouter(id) {
      if (id == '1' && this.status != id) {
        this.$router.push('/home')
      } else if (id == '2' && this.status != id) {
        this.$router.push('/quotation')
      } else if (id == '3' && this.status != id) {
        this.$router.push('/transaction')
      } else if (id == '4' && this.status != id) {
        this.$router.push('/assets')
      } else if (id == '5' && this.status != id) {
        this.$router.push('/user')
      }
    },
  },
  created() {},
}
</script>

<style lang="less" scoped>
.tabbar {
  box-sizing: border-box;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1.306667rem;
  background-color: #212126;
  display: flex;
  align-items: center;
  z-index: 66;
  div {
    height: 100%;
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
    span {
      color: #bbbbbb;
      font-size: 0.293333rem;
      margin-top: 0.1rem;
    }
    img {
      width: 0.466667rem;
    }
    .choose {
      color: #eec67f;
    }
  }
}
</style>
